<template>
  <div class="notLogin" v-if="!loginState">
    <div class="card" v-for="(item, index) in coffee_shops" :key="index">
      <div class="card_title">
        <p>{{ item.name }}</p>
      </div>

      <button @click="login">LINE 登入</button>
    </div>
    <div class="card code">
      <div class="buttons_container">
        <button v-for="(item, id) in tap_buttons" :key="id">
          <p>{{ item.name }}</p>
        </button>
      </div>
      <div class="to_login_hint">
        <p>請用 <span style="color: green">LINE</span> 登入</p>
      </div>
      <div class="to_login_hint">
        <p>免費領取VIP會員卡</p>
      </div>
    </div>

    <div class="link_button_container">
      <div
        class="link_button"
        v-for="(item, index) in button_array"
        :key="index"
      >
        <img :src="item.icon" class="link_button_icon" />
        <p>{{ item.name }}</p>
      </div>
    </div>
  </div>
  <div v-else>
    <p>{{  getShopType }}</p>
  </div>
</template>

<script>
import axios from "axios";
import ironman from "./assets/member.png";
export default {
  beforeMount() {
    this.handleLoginCallback();
  },
  computed: {
    loginState() {
      return this.$store.state.isLogin;
    },
    getUserName() {
      return this.$store.state.userName;
    },
    getShopType() {
      return this.$store.state.shopType;
    },
  },
  data() {
    return {
      coffee_shops: [
        {
          name: "Bryant咖啡館",
          loginRouter: "Bryant",
        },
        {
          name: "cama coffee",
          loginRouter: "cama",
        },
        {
          name: "咖啡深藏",
          loginRouter: "soul",
        },
      ],
      tap_buttons: [
        {
          name: "會員QR碼",
          id: "1",
        },
        {
          name: "手機載具",
          id: "2",
        },
      ],
      button_array: [
        {
          icon: ironman,
          name: "會員",
        },
        {
          icon: ironman,
          name: "優惠卷",
        },
        {
          icon: ironman,
          name: "積點",
        },
        {
          icon: ironman,
          name: "價格",
        },
        {
          icon: ironman,
          name: "集點卡",
        },
        {
          icon: ironman,
          name: "抽獎",
        },
        {
          icon: ironman,
          name: "寄存卡",
        },
        {
          icon: ironman,
          name: "行銷活動",
        },
        {
          icon: ironman,
          name: "儲值信託",
        },
        {
          icon: ironman,
          name: "履約票卷",
        },
      ],
    };
  },
  methods: {
    login(routerType) {
      const builtUrl = this.buildUrl(routerType);
      window.location.href = builtUrl;
      console.log("start login", builtUrl);
    },
    buildUrl(routerType) {
      let baseUrl = "https://access.line.me/oauth2/v2.1/authorize?";
      const params = {
        response_type: "code",
        client_id: "1661441879",
        redirect_uri: "https://qkqp3qr2.github.io/blankvue/",
        scope: "openid%20profile",
      };

      params.shop = routerType;
      const queryString = Object.entries(params)
        .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
        .join("&");

      return `${baseUrl}${queryString}`;
    },
    async handleLoginCallback() {
      const code = new URLSearchParams(window.location.search).get("code");
      if (code) {
        const accessToken = await this.getAccessToken(code);
        await this.getUserProfile(accessToken);
        this.$store.commit("setLoginState");
        console.log("login successful");
        // const redirectURL = await this.redirectTo();
        // window.location.href = redirectURL;
      } else {
        console.error("Authorization code not found.");
      }
    },
    async getAccessToken(code) {
      try {
        const response = await axios.post(
          "https://api.line.me/oauth2/v2.1/token",
          new URLSearchParams({
            grant_type: "authorization_code",
            code,
            client_id: "1661441879",
            client_secret: "4f03d9065b14f7d6e394a0f20b0c53e3",
            redirect_uri: "https://qkqp3qr2.github.io/blankvue/",
          }).toString(),
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
          }
        );

        return response.data.access_token;
      } catch (error) {
        console.error("Failed to get access token:", error);
        throw error;
      }
    },
    async getUserProfile(accessToken) {
      try {
        const response = await axios.get("https://api.line.me/v2/profile", {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        });

        const userProfile = response.data;
        console.log(userProfile);
        this.$store.commit("setUserName", userProfile.displayName);
      } catch (error) {
        console.error("Failed to get user profile:", error);
      }
    },
    async redirectTo() {
      const shop = new URLSearchParams(window.location.search).get("shop");
      if (shop) {
        this.$store.commit("setShopType", shop);
      } else {
        this.$store.commit("setShopType", 'error');
      } 
    },
  },
};
</script>

