import { createStore } from "vuex";

const store = createStore({
  state() {
    return {
      isLogin: false,
      userName: "",
      shopType: "",
    };
  },
  mutations: {
    setLoginState(state) {
      state.isLogin = true;
    },
    setUserName(state, name) {
      state.userName = name;
    },
    setShopType(state, type) {
      state.shopType = type;
    },
  },
});

export default store;
